header {
  background: linear-gradient(to right, rgb(30, 113, 238), rgb(20, 151, 212));
  background: linear-gradient(to top, #00ffff, #1497d4);
  width: 80px;
  height: 100vh;
  position: fixed;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color: white;
  padding: 15px 10px;
  flex-direction: column;
  box-shadow: 0 0 35px rgba(0, 0, 0, 0.1), 0 0 35px rgba(0, 0, 0, 0.1),
    0 0 35px rgba(0, 0, 0, 0.1);

  @media screen and (max-width: 700px) {
    width: 100%;
    height: 100px;
    background: linear-gradient(to top left, #00ffff, #1497d4);
    padding: 0 20px;
  }

  .branding {
    width: 100%;
    text-align: center;
    font-weight: bold;

    @media screen and (max-width: 700px) {
      padding-top: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 100%;
    }
  }

  .addWebsite {
    position: absolute;
    bottom: 30px;
    left: 45%;
    transform: translateX(-50%);

    @media screen and (max-width: 700px) {
      display: none;
    }

    &:hover form {
      opacity: 1;
      pointer-events: auto;
    }

    .addThis {
      border: 2px solid white;
      padding: 32px 25px;
      padding: 25px 18px;
      font-size: 12px;
      font-weight: bold;
      line-height: 100%;
      text-transform: uppercase;
      text-align: center;
      border-radius: 50px;
      background: rgba(8, 132, 214, 0.4);
      cursor: pointer;
      box-shadow: 2px 2px 0px 0 rgb(25, 222, 248),
        4px 4px 0px 0 rgb(255, 29, 135), 6px 6px 0px 0 rgb(206, 255, 29);
    }
  }

  i {
    color: white;
  }

  .logo {
    text-shadow: -2px -2px 0px rgb(25, 222, 248), 2px 2px 0px rgb(255, 29, 135),
      4px 4px 0px rgb(206, 255, 29);
    margin-bottom: -8px;
    margin-left: -12px;
    font-size: 36px;
    animation: bling 3s 1s infinite step-end;

    @media screen and (max-width: 700px) {
      margin-left: -2px;
    }
  }

  .home {
    text-decoration: none;
    color: white;
    text-shadow: 1px 1px 0px rgb(25, 222, 248);
  }

  h1 {
    font-size: 15px;
    padding-top: 10px;
    text-transform: uppercase;
    font-weight: 600;
    text-shadow: -1px -1px 0px #19def8, 1px 1px 0px #ff1d87, 1.5px 1.5px 0px #ceff1d;

    @media screen and (max-width: 700px) {
      padding-top: 0;
      text-align: left;
    }

    span {
      display: block;
    }
  }

  p {
    margin-top: 20px;
    font-size: 10px;
    font-style: italic;

    @media screen and (max-width: 700px) {
      margin-top: 0;
      font-weight: bold;
    }
  }

  nav {
    left: 0px;
    width: 100%;
    padding-top: 30px;

    @media screen and (max-width: 700px) {
      display: none;
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      text-align: center;

      li {
        margin-bottom: 5px;
        width: 100%;

        a {
          color: white;
          // background: rgba(53, 134, 255,0.6);
          text-decoration: none;
          font-size: 12px;
          display: block;
          width: 100%;
          text-align: center;
          letter-spacing: 1px;
          font-weight: 600;
          padding: 10px 0;
          text-transform: uppercase;

          &:hover {
            background: rgba(8, 132, 214, 0.9);
          }
        }

        &:nth-child(2) {
          a {
            // border: 1px solid white;
          }
        }

        &:first-child {
          a {
            background: white;
            color: rgb(85, 85, 223);
            padding: 10px;
          }
        }
      }
    }
  }
}

@keyframes bling {
  0% {
    color: white;
    text-shadow: -2px -2px 0px rgb(25, 222, 248), 2px 2px 0px rgb(255, 29, 135),
      4px 4px 0px rgb(206, 255, 29);
  }
  15% {
    color: rgb(206, 255, 29);
    text-shadow: -2px -2px 0px white, 2px 2px 0px rgb(25, 222, 248),
      4px 4px 0px rgb(255, 29, 135);
  }
  30% {
    color: rgb(255, 29, 135);
    text-shadow: -2px -2px 0px rgb(206, 255, 29), 2px 2px 0px white,
      4px 4px 0px rgb(151, 234, 245);
  }

  45% {
    color: rgb(151, 234, 245);
    text-shadow: -2px -2px 0px rgb(255, 29, 135), 2px 2px 0px rgb(206, 255, 29),
      4px 4px 0px white;
  }
  60% {
    color: white;
    text-shadow: -2px -2px 0px rgb(25, 222, 248), 2px 2px 0px rgb(255, 29, 135),
      4px 4px 0px rgb(206, 255, 29);
  }
}
