article {
  height: auto;
  padding: 3% 0;
  flex-grow: 1;
  flex-basis: 33.333333%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 300ms ease;
  overflow: hidden;

  img {
    width: auto;
    height: 100px;
    bottom: -20px;
    right: 20px;
    position: absolute;
    z-index: -1;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
    transition: transform 300ms ease;
  }

  &:hover {
    flex-grow: 3;
    flex-basis: 60%;

    .link {
      opacity: 1;
    }

    .category {
      letter-spacing: 3px;
    }
  }

  &.white {
    div {
      color: white;
    }
  }

  &:after {
    content: "â†’";
    position: absolute;
    top: 5px;
    z-index: 99;
    right: 10px;
    transform: rotate(-45deg);
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    color: black;
    opacity: 0;
    font-weight: bold;
    // text-shadow: 1px 1px 0 white;
    font-size: 20px;
    transition: all 300ms ease;
  }
}

.website-wrapper {
  display: block;
  text-decoration: none;
  color: #000;
  width: 100%;
  z-index: 2;
  padding: 20px 30px;

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: all 500ms ease;
    background: rgba(0, 0, 0, 0.2);
    opacity: 0;
    z-index: 0;
  }
}

.info {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.eyebrow {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.title {
  font-size: 40px;
  font-weight: 400;
  margin-top: 0.5rem;
}

.link {
  font-size: 14px;
  font-weight: 700;
  opacity: 0;
  transition: all 300ms ease;
}

.desc {
  font-size: 16px;
  resize: none;
  max-width: 60%;
  width: 100%;
  font-weight: 400;
  margin-top: 0.5rem;
  line-height: 120%;
}

.category {
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: 600;
  text-transform: uppercase;
  transition: letter-spacing 300ms ease;
}

.controls {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateX(20px);
  right: -16px;
  z-index: 12;
  transition: all 500ms ease;
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  button {
    border: none;
    cursor: pointer;
    padding: 5px 10px;
    background: white;
    color: #00446e;
    font-family: "Work Sans" !important;
    transition: all 300ms ease;
    border-radius: 10px;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);

    &:not(:first-child) {
      margin-top: 10px;
    }
  }
}

@keyframes spin {
  0% {
    top: 5px;
    right: 10px;
  }

  50% {
    top: -11px;
    right: -6px;
  }

  100% {
    top: 5px;
    right: 10px;
  }
}
